import React, { useState } from 'react';
import history from '../../utils/browserHistory';

import { createPost } from '../../container/post';

import TopHeader from '../../components/TopHeader';

import {
  PostWritePage,
  PostWriteForm,
  // ImageForm,
  // ImageInput,
  // Image,
  InputBox,
  Input,
  Textarea,
  WriteBtn,
} from './style';
import useImageInputForm from '../../components/useImageInputForm';
import { addImage } from '../../container/image';

function PostWrite() {
  const [postField, setPostField] = useState({
    title: '',
    content: '',
    totalPrice: '',
    minParti: '',
    chatUrl: '',
  });
  const {
    FormComponent,
    imageFormField,
    setImageFormField,
  } = useImageInputForm(5);

  const { title, content, totalPrice, minParti, chatUrl } = postField;

  const changeState = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
  ) => {
    const { name, value } = e.target;

    setPostField({
      ...postField,
      [name]: value,
    });
  };

  const onClickWriteBtn = async function (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>,
  ) {
    const target = e.currentTarget;
    target.disabled = true;
    target.classList.add('on');

    const res = await createPost({
      title,
      content,
      totalPrice: Number(totalPrice),
      minParti: Number(minParti),
      chatUrl,
    });
    // console.log('createPost!!', res);

    if (res?.success === false) {
      alert(res.msg);

      target.disabled = false;
      target.classList.remove('on');
      return;
    }
    setPostField({
      title: '',
      content: '',
      totalPrice: '',
      minParti: '',
      chatUrl: '',
    });

    if (!res?.data.post_id) {
      target.disabled = false;
      target.classList.remove('on');
      return;
    }
    const addImageRes = await Promise.all(
      imageFormField.map((image) =>
        addImage(res?.data.post_id, { file: image }),
      ),
    );
    // console.log('addImageRes', addImageRes);

    if (addImageRes.map((r) => r?.success).includes(false)) {
      alert(JSON.stringify(addImageRes));

      target.disabled = false;
      target.classList.remove('on');
      return;
    }

    setImageFormField([]);

    target.disabled = false;
    target.classList.remove('on');

    alert(res?.msg);
    history.push('/main');
  };

  return (
    <PostWritePage>
      <TopHeader pageName="ÎÇòÎàÑÍ∏∞ Í∞úÏÑ§" />
      <PostWriteForm>
        {FormComponent}
        {/* <ImageForm>
          <ImageInput />
          <Image />
          <Image />
        </ImageForm> */}

        <Input
          name="title"
          placeholder="Ï†úÎ™©"
          value={title}
          onChange={changeState}
        />
        <Input
          name="chatUrl"
          placeholder="Ïò§ÌîàÏ±ÑÌåÖÎ∞© ÎßÅÌÅ¨"
          value={chatUrl}
          onChange={changeState}
        />
        <InputBox>
          <Input
            name="totalPrice"
            placeholder="Í∞ÄÍ≤©(Ïõê)"
            value={totalPrice}
            onChange={changeState}
          />
          <Input
            name="minParti"
            placeholder="ÎÇòÎàÑÍ∏∞ Í∞úÏàò(Í∞ú)"
            value={minParti}
            onChange={changeState}
          />
        </InputBox>

        <Textarea
          className="content"
          name="content"
          placeholder="ÎÇ¥Ïö©"
          value={content}
          onChange={changeState}
        />

        {/* <PostInfoBox>
          <InputImage onClick={() => alert('...')}>Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú</InputImage>
          <InputBox>
            <Input />
            <Input
              name="content"
              placeholder="ÎÇ¥Ïö©"
              value={content}
              onChange={changeState}
              style={{ height: '90px' }}
            />
            <Input
              name="totalPrice"
              placeholder="Ï¥ù Í∞ÄÍ≤©"
              value={totalPrice}
              onChange={changeState}
            />
            <Input
              name="nanumPrice"
              placeholder="ÎÇòÎàîÍ∞Ä"
              value={nanumPrice}
              onChange={changeState}
            />
            <Input
              name="minParti"
              placeholder="ÏµúÏÜåÏù∏Ïõê"
              value={minParti}
              onChange={changeState}
            />
            <Input
              name="maxParti"
              placeholder="ÏµúÎåÄÏù∏Ïõê"
              value={maxParti}
              onChange={changeState}
            />
            <Input
              name="chatUrl"
              placeholder="Ï≤¥ÌåÖÎ∞© ÎßÅÌÅ¨"
              value={chatUrl}
              onChange={changeState}
            />
          </InputBox>
        </PostInfoBox> */}

        <WriteBtn type="button" onClick={onClickWriteBtn}>
          ÎÇòÎàÑÍ∏∞ Í∞úÏÑ§ ÏôÑÎ£å
        </WriteBtn>
      </PostWriteForm>
    </PostWritePage>
  );
}

export default PostWrite;
